@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 2rem;

  background: linear-gradient(to right top, #65dfc9, #6cdbeb);
}

@media only screen and (max-width: 600px) {
  body {
    padding: 1rem;
  }
}
@media only screen and (max-width: 768px) {
  body {
    padding: 1rem;
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* border: 1px solid black; */
  background-color: darkviolet;
}

.navBar {
  display: flex;
  background-color: #00c1d4;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  /* backdrop-filter: blur(2rem); */
  font-family: "Roboto";
}

.nav__item {
  padding: 1.5rem 1rem;
  font-size: larger;
}

.nav__item:hover {
  border-bottom: 2px solid black;
  cursor: pointer;
}

.linkTag {
  text-decoration: none;
  color: black;
}

.left-nav {
  display: flex;
  justify-content: space-between;
}

.right-nav {
  display: flex;
}

@media only screen and (min-width: 768px) {
  .nav__item {
    padding: 1rem 1rem;
    font-size: large;
  }

  .navBar {
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 600px) {
  .nav__item {
    padding: 1rem 1rem;
    font-size: small;
  }

  .navBar {
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media only screen and (min-width: 992px) {
  .navBar {
    display: flex;
    background-color: #00c1d4;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    /* backdrop-filter: blur(2rem); */
    font-family: "Roboto";
  }

  .nav__item {
    padding: 1.5rem 1rem;
    font-size: larger;
  }
}

.newsItems {
  height: 500px;
  width: 30%;
  margin-bottom: 5rem;
}

.card {
  height: 100%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  padding: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 0.5rem;
}

img {
  height: 35%;
  width: 100%;
  border-radius: 0.5rem;
}

.cardBtn {
  padding: 0.5rem;
  border-radius: 0.4rem;
  border: none;
  text-decoration: none;
  background-color: rgb(248, 0, 145);
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  float: right;
  margin-bottom: 10px;
}

.cardBtn:hover {
  background-color: rgba(247, 36, 159, 0.835);
  cursor: pointer;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 25px 25px 25px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

/*   ///////////////    */
@media only screen and (max-width: 768px) {
  .newsItems {
    height: 500px;
    width: 48%;
    margin-bottom: 5rem;
  }
}

@media only screen and (max-width: 600px) {
  .newsItems {
    height: 420px;
    width: 100%;
    margin-bottom: 5rem;
    font-size: small;
  }

  img {
    border-radius: 0.5rem;
  }
}

@media only screen and (min-width: 992px) {
  .newsItems {
    height: 500px;
    width: 30%;
    margin-bottom: 5rem;
  }

  img {
    height: 35%;
    width: 100%;
    border-radius: 0.5rem;
  }
}

.newsContainer {
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  -webkit-backdrop-filter: blur(5rem);
          backdrop-filter: blur(5rem);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5rem;
}

@media only screen and (max-width: 600px) {
  .newsContainer {
    padding: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .newsContainer {
    padding: 3rem;
  }
}

