.newsItems {
  height: 500px;
  width: 30%;
  margin-bottom: 5rem;
}

.card {
  height: 100%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  padding: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 0.5rem;
}

img {
  height: 35%;
  width: 100%;
  border-radius: 0.5rem;
}

.cardBtn {
  padding: 0.5rem;
  border-radius: 0.4rem;
  border: none;
  text-decoration: none;
  background-color: rgb(248, 0, 145);
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  float: right;
  margin-bottom: 10px;
}

.cardBtn:hover {
  background-color: rgba(247, 36, 159, 0.835);
  cursor: pointer;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 25px 25px 25px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

/*   ///////////////    */
@media only screen and (max-width: 768px) {
  .newsItems {
    height: 500px;
    width: 48%;
    margin-bottom: 5rem;
  }
}

@media only screen and (max-width: 600px) {
  .newsItems {
    height: 420px;
    width: 100%;
    margin-bottom: 5rem;
    font-size: small;
  }

  img {
    border-radius: 0.5rem;
  }
}

@media only screen and (min-width: 992px) {
  .newsItems {
    height: 500px;
    width: 30%;
    margin-bottom: 5rem;
  }

  img {
    height: 35%;
    width: 100%;
    border-radius: 0.5rem;
  }
}
