@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

.navBar {
  display: flex;
  background-color: #00c1d4;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  /* backdrop-filter: blur(2rem); */
  font-family: "Roboto";
}

.nav__item {
  padding: 1.5rem 1rem;
  font-size: larger;
}

.nav__item:hover {
  border-bottom: 2px solid black;
  cursor: pointer;
}

.linkTag {
  text-decoration: none;
  color: black;
}

.left-nav {
  display: flex;
  justify-content: space-between;
}

.right-nav {
  display: flex;
}

@media only screen and (min-width: 768px) {
  .nav__item {
    padding: 1rem 1rem;
    font-size: large;
  }

  .navBar {
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 600px) {
  .nav__item {
    padding: 1rem 1rem;
    font-size: small;
  }

  .navBar {
    justify-content: center;
    flex-wrap: wrap;
  }
}

@media only screen and (min-width: 992px) {
  .navBar {
    display: flex;
    background-color: #00c1d4;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    /* backdrop-filter: blur(2rem); */
    font-family: "Roboto";
  }

  .nav__item {
    padding: 1.5rem 1rem;
    font-size: larger;
  }
}
