.newsContainer {
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  backdrop-filter: blur(5rem);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5rem;
}

@media only screen and (max-width: 600px) {
  .newsContainer {
    padding: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .newsContainer {
    padding: 3rem;
  }
}
